<template>
  <router-view
    :socket="socket"
    :question="question"
    :answers="answers"
    :answerStatistics="answerStatistics"
    :studentAnswered="studentAnswered"
  />
</template>

<script>
export default {
  components: {},
  data() {
    return {
      socket: {},
      question: {},
      answers: [],
      answerStatistics: [],
      studentAnswered: 0,
    };
  },
  created() {
    this.socket = this.connectSocket();
    this.registerEvent(this.socket);
  },
  methods: {
    connectSocket() {
      const socketUrl = "http://localhost:8082";
      var socket = io.connect(socketUrl);
      socket
        .on("connected", (data) => {
          console.log(data);
        })
        .emit("registerHostSocket", {
          challengeId: this.$route.query.challengeId,
          token: this.$store.state.token,
        })
        .on("registerSuccess", (data) => {
          // console.log(data);
          this.student = data;
        });

      socket.on("joinError", (data) => {
        this.$eventBus.$emit("nofication", {
          message: "Cann't connect to room!!!",
          status: "error",
        });
        throw "Cannot connect socket";
      });

      return socket;
    },
    registerEvent(socket) {
      socket.on("startChallenge", () => {
        this.$router.push({
          name: "host.startChallenge",
          query: {
            challengeId: this.$route.query.challengeId,
          },
        });
      });
      this.socket.on("publishQuestion", (data) => {
        this.question = data.question;
        this.answers = data.answers;
        this.studentAnswered = 0;

        this.$router.push({
          name: "host.publishQuestionReady",
          query: {
            challengeId: this.$route.query.challengeId,
            questionId: this.question.id,
          },
        });
      });

      this.socket.on("showCorrectAnswer", (data) => {
        this.answers = data.answers;
        this.answerStatistics = data.answerStatistics;

        this.$router
          .push({
            name: "host.publishAnswerStatistics",
            query: {
              challengeId: this.$route.query.challengeId,
              questionId: this.$route.query.questionId,
            },
          })
          .catch((err) => err);
      });

      this.socket.on("studentAnswer", () => {
        this.studentAnswered++;
        console.log(this.studentAnswered);
      });
      this.socket.on("endChallenge", () => {
        this.$router
          .push({
            name: "host.challengeFinish",
            query: {
              challengeId: this.$route.query.challengeId,
            },
          })
          .catch((err) => err);
      });
    },
  },
};
</script>

<style>
</style>


