<template>
  <QuestionLayout>
    <h1>Get question...</h1>
  </QuestionLayout>
</template>

<script>
import HostPlayService from "@/services/HostPlayService";
import QuestionLayout from "@/components/host/organize/QuestionLayout";

export default {
  components: {
    QuestionLayout,
  },
  created() {
    HostPlayService.publishNextQuestion({
      challengeId: this.$route.query.challengeId,
    });
  },
};
</script>

<style>
</style>



