<template>
  <v-flex xs12 sm12 md12>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :options.sync="options"
      :server-items-length="totalDesserts"
      :loading="loading"
      :footer-props="{
        'items-per-page-options': [
          10,
          20,
          30,
          50,
          100,
          {
            text: 'All',
            value: 999,
          },
        ],
      }"
      disable-sort
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>List Challenge</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="1000px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                New Item
              </v-btn>
            </template>
            <v-card >
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        v-model="editedItem.title"
                        label="Title"
                        counter="255"
                        :error-messages="titleErrors"
                        required
                        @input="$v.editedItem.title.$touch()"
                        @blur="$v.editedItem.title.$touch()"
                      >
                      </v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="3">
                      <v-switch
                        v-model="editedItem.randomAnswer"
                        label="Random Answer"
                      >
                      </v-switch>
                    </v-col>
                    <v-col cols="12" sm="6" md="3">
                      <v-switch
                        v-model="editedItem.randomQuest"
                        label="Random Question"
                        
                      >
                      </v-switch>
                    </v-col>
                    <v-col cols="12" sm="6" md="3">
                      <v-select
                        :items="challengeStatus"
                        v-model="editedItem.challengeStatus"
                        label="Challenge status"
                        outlined
                      ></v-select>
                    </v-col>

                    <v-col cols="12" sm="6" md="3">
                      <v-file-input accept="image/*" label="Cover image">
                      </v-file-input>
                    </v-col>
                    <v-col>
                     <v-img :lazy-src="require(`@/assets/img/hutech-logo.png`)" :src="editedItem.coverImage" class="my-1 rounded" v-on:error="src=require(`@/assets/img/hutech-logo.png`)"/>
                    </v-col>
                    
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">
                Are you sure you want to delete this item?
              </v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">
                  OK
                </v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="viewListQuestion(item)">
          view_list
        </v-icon>
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small class="mr-2" @click="deleteItem(item)">
          mdi-delete
        </v-icon>

        <v-icon small class="mr-2" @click="play(item)"> play_arrow </v-icon>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize"> Reset </v-btn>
      </template>
      <template v-slot:[`item.title`]="{ item }">
        <span class="d-inline-block text-truncate" style="max-width: 300px">
          {{ item.title }}
        </span>
      </template>
      <template v-slot:[`item.coverImage`]="{ item }">
        <ImageDataTable :src="item.coverImage" />
      </template>
      <template v-slot:[`item.createdDate`]="{ item }">
        <DateFormater :date="item.createdDate" />
      </template>
    </v-data-table>
  </v-flex>
</template>

<script>
import HostManageService from "@/services/HostManageService";
import HostPlayService from "@/services/HostPlayService";

import ImageDataTable from "@/components/ImageDataTable";
import DateFormater from "@/components/DateFormater";

import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  minLength,
  minValue,
} from "vuelidate/lib/validators";

export default {
  components: {
    DateFormater,
    ImageDataTable,
  },
  // validate
  mixins: [validationMixin],
  validations: {
    editedItem: {
      title: { required, maxLength: maxLength(255) },
    },
  },

  // data
  data: () => ({
    imageLinkReturn: "http://",
    challengeStatus: ["BUILDING", "WAITING", "IN_PROGRESS", "LOCKED", "ENDED"],
    answerOption: ["SINGLE_SELECT", "MULTI_SELECT"],
    point: ["STANDARD", "DOUBLE_POINTS", "NO_POINTS"],
    radioGroup: 1,
    dialog: false,
    dialogDelete: false,
    headers: [
      { text: "Id", value: "id", align: "start", sortable: true },
      { text: "Title", value: "title" },
      { text: "Cover", value: "coverImage" },
      { text: "Created date", value: "createdDate" },
      { text: "Owner", value: "owner" },
      { text: "Random answer", value: "randomAnswer" },
      { text: "Random quest", value: "randomQuest" },
      { text: "Status", value: "challengeStatus" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      Id: 0,
      title: "",
      coverImage: "",
      randomAnswer: false,
      randomQuest: false,
      challengeStatus: "BUILDING",
    },
    defaultItem: {
      Id: 0,
      title: "",
      coverImage: "",
      randomAnswer: false,
      randomQuest: false,
      challengeStatus: "BUILDING",
    },
    totalDesserts: 0,
    loading: true,
    options: {},
  }),
  computed: {
    titleErrors() {
      const errors = [];
      if (!this.$v.editedItem.title.$dirty) return errors;
      !this.$v.editedItem.title.required &&
        errors.push("Question title required!");
      !this.$v.editedItem.title.maxLength &&
        errors.push("Question title length must less than 255!");
      return errors;
    },

    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    options: {
      handler() {
        this.getDataFromApi();
      },
      deep: true,
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {},

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.loading = true;
      HostManageService.updateChallenge({
        id: this.editedItem.id,
        isNonDeleted: false,
      });

      this.desserts.splice(this.editedIndex, 1);
      this.closeDelete();
      this.loading = false;
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      this.$v.$touch();
      if (this.$v.$anyError) {
        return;
      }

      if (this.editedIndex > -1) {
        HostManageService.updateChallenge(Object.assign({}, this.editedItem));
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        HostManageService.addChallenge(Object.assign({}, this.editedItem)).then((response) => this.desserts.unshift(response.data));
      }
      this.close();
    },
    getDataFromApi() {
      console.log(this.options);
      this.loading = true;
      HostManageService.findAllChallenge(this.options).then((response) => {
        console.log(response.data);
        this.desserts = response.data.list;
        this.totalDesserts = response.data.totalElements;
        this.loading = false;
      });
    },
    viewListQuestion(item) {
      this.$router.push({
        name: "host.listQuestion",
        query: { challengeId: item.id },
      });
    },
    play(item) {
      // join a game

      HostPlayService.openChallenge({
        challengeId: item.id,
      }).then(() => {
        this.$router.push({
          name: "waitingRoom",
          query: { challengeId: challengeId },
        });
      });
    },
  },
};
</script>


